@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  @apply font-mono bg-slate-950 text-slate-200 min-h-screen;
}

#root {
  @apply min-h-screen;
}

::-webkit-scrollbar {
  @apply w-2 h-2;
}

::-webkit-scrollbar-track {
  @apply bg-slate-800;
}

::-webkit-scrollbar-thumb {
  @apply bg-slate-600 rounded;
}

::-webkit-scrollbar-thumb:hover {
  @apply bg-slate-500;
}
